.profile {
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
    padding: 20px; 
    position: relative;
}

/*Executive profile details*/
.profile_details {
    align-content: center;
    justify-content: space-evenly;
    line-height: 13pt;
    margin: 25px 0px 70px 0px;
    text-align: center;
}

/*Executive social media links*/
.icon_bar {
    align-content: center;
}
.icon {   
    border: 0;
    height: 25px;
    width: 25px;
    margin: 0 auto;
    margin-left: 10px;
    margin-right: 10px;
    align-self: center;
}

/*Executive names*/
.profile_text_name {
    align-content: center;
    color: #feb14b;
    font-family: 'Playfair Display', serif;
    font-size: 24pt;
