@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&family=Playfair+Display&display=swap');
/*font-family: 'Playfair Display', serif;
font-family: 'Montserrat', sans-serif;*/

h1 {
    color: #feb14b;
    font-family: 'Playfair Display', serif;
    font-size: 48pt;
}

h2 {
    color: #feb14b;
    font-family: 'Montserrat', sans-serif;
    font-size: 30pt;
    text-transform: uppercase;
    padding-bottom: 20px;
    text-align: center;
}

.header{
    padding-top: 50px;
    padding-bottom: 20px;
    text-align: center;
}

.subheader {
    font-family: 'Montserrat', sans-serif;
    font-size: 13pt;
    color: grey;
}

p {
    font-family: 'Montserrat', sans-serif;
    font-size: 10pt;
    margin-top: 0;
}

.body_bold {
    font-family: 'Montserrat', sans-serif;
    font-size: 10pt;
    font-weight: bold;
}

.centre {
    text-align: center;
}

.button {
    font-family: 'Montserrat', sans-serif;
    background-color:#e85f5c;
    border-radius: 60px;
    border-style: none;
    color: white;
    padding: 10px 45px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}

.button:hover {
    background-color: #feb14b;
    color: #ffffff;
}

/*-----------------------------------Navbar-----------------------------------*/
.bg-custom{
    background: #fff7ef;
    font-size: 10pt;
}

.nav{
    height: 70px;
}

.navbar-nav{
    color: #313638;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    text-decoration: none;
    float: right;
}

.nav-item{
    float:right;
    color: #313638;
    padding: 5px;
    text-decoration: none;
    letter-spacing: 1.5px;
}

.nav-item a{
    color: #313638;
    padding: 10px 15px;
    text-decoration: none;
}

.nav-item a:hover{
    color: #e85f5c;
    padding: 10px 15px;
    text-decoration: none;
}
/*----------------------------------Footer------------------------------------*/
footer {
    margin: 0;
    padding: 0;
}

.footer-top {
    margin: 0 auto;
}

.footer-links {
    list-style-type: none;
    margin: 20px auto;
    padding: 0;
    align-self: center;
    width: 60%;
}

.footer-item {
    display: inline;
    font-family: 'Montserrat', sans-serif;
    padding: 16px;
    color: #313638;
}

.footer-copyright {
    font-family: 'Montserrat', sans-serif;
}


.footer-copyright, a {
    color: #313638;
}

.footer-item a:hover {
    color: #feb14b;
    text-decoration: none;
}

#copyright {
    padding: 0px;
}
